<template>
  <div>
    <h2>Recipe Home</h2>
    <Markdown :mmark="mmark" />
  </div>
</template>

<script>
// @ is an alias to /src

import firebase from "firebase";
import Markdown from "../components/Markdown.vue";
export default {
  name: "home",
  components: {
    Markdown
  },
  data() {
    return {
      mmark: "",
      db: ""
    };
  },
  created() {
    this.db = firebase.database();
    let that = this;
    let mmark_pre = "";
    this.db.ref("recipe/md").on("value", snapshot => {
      for (let sval of snapshot.val()) {
        if (sval !== undefined) {
          mmark_pre += sval["content"];
        }
      }
      that.mmark = mmark_pre;
    });
    //
    // let recipedb = this.db.ref("recipedb");
    // recipedb.on(
    //   "value",
    //   function(snapshot) {
    //     console.log(snapshot.val());
    //   },
    //   function(errorObject) {
    //     console.log("The read failed: " + errorObject.code);
    //   }
    // );
  }
};
</script>
